.toolbox {
  display: flex;
  z-index: 99999;
  justify-content: space-between;
  max-width: 450px;
  padding: 12px;
}

.timeframe-selector {
  display: flex;
  justify-content: center;
}

.timeframe-button {
  background-color: #333;
  color: white;
  border: 1px solid #555;
  margin: 0 5px;
  padding: 10px 10px;
  cursor: pointer;
  border-radius: 4px;
}

.timeframe-button:hover {
  transition: 0.15s all;
  background-color: #444;
}

.timeframe-button.active {
  background-color: #555;
}

.main-view {
  display: flex;
  color: white;
}

.trading-view {
  flex: 4;
}

.order-book {
  flex: 2;
}

@media screen and (max-width: 768px) {
  .main-view {
    flex-direction: column;
  }
}
