.orderbooks {
  color: white;
  background-color: #222;
  padding: 12px;
}

.histogram {
  width: 100%;
}

.histogram-row {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  font-size: medium;
}
.histogram-bar {
  position: absolute;
  height: 100%;
  right: 0;
  z-index: 1;
  opacity: 0.2;
}

.histogram-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 2px 10px;
  z-index: 2;
}

.price,
.amount,
.total {
  display: inline-block;
  min-width: 60px;
  width: 33%;
}

.amount {
  text-align: center;
}

.total {
  text-align: end;
}
